* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Fjalla One', 'Teko', Roboto, Oxygen-Sans, Ubuntu,
    Cantarell, 'Helvetica Neue', sans-serif;
  font-family: 'Fjalla One', sans-serif;
  font-family: 'Teko', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url(https://i.artfile.me/wallpaper/05-10-2017/1920x1080/cvety-tyulpany-butony-chernyj-fon-1244099.jpg);
  color: #fdf4f4;
  line-height: 1.5;
  background-size: cover;
  line-height: 1.5;
}

li {
  list-style: none;
}
input,
button,
a,
label {
  font-size: 30px;
  font-family: 'Fjalla One';
  color: #28a9c0;
  text-decoration: none;
  line-height: 1.2;
  border-radius: 5px;
}

img {
  display: block;
  max-width: 100%;
}
.timer {
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 1000px;

  display: flex;
  align-items: center;
  justify-content: space-around;

  font-family: 'Permanent Marker', sans-serif;
  font-size: 30px;
  color: #4a6b8a;
}
.form {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-end;
  align-content: center;
}
.form button {
  width: 200px;
  background-color: rgb(238, 203, 203);
  border-radius: 5px;
}
